/* Overlay (Simulated Redirect) */
.redirect-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.2);
  backdrop-filter: blur(4px);
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.redirect-overlay.hidden {
  display: none;
}

.redirect-overlay:not(.hidden) {
  opacity: 1;
}

.redirect-content {
  background: #fff;
  border-radius: 1rem;
  padding: 2rem;
  position: relative;
  width: 90%;
  max-width: 360px;
  box-shadow: 0 20px 25px -5px rgba(0,0,0,0.1), 0 10px 10px -5px rgba(0,0,0,0.04);
  transform: scale(0.95);
  opacity: 0;
  transition: all 0.3s ease;
}

.redirect-overlay:not(.hidden) .redirect-content {
  transform: scale(1);
  opacity: 1;
}

.close-redirect {
  position: absolute;
  top: 1rem;
  right: 1rem;
  opacity: 0.5;
  transition: opacity 0.2s ease;
}

.close-redirect:hover {
  opacity: 1;
}

/* Redirect Animation */
.redirect-animation {
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 auto 1.5rem;
}

.pulse-ring {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 3px solid #5B47FB;
  opacity: 0;
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

.institution-avatar {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 64px;
  height: 64px;
  border-radius: 16px;
  overflow: hidden;
  background: #f3f4f6;
  box-shadow: 0 4px 6px -1px rgba(0,0,0,0.1), 0 2px 4px -1px rgba(0,0,0,0.06);
}

.institution-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Redirect Text */
.redirect-text {
  text-align: center;
}

.redirect-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #111827;
  margin-bottom: 0.25rem;
}

.redirect-subtitle {
  font-size: 0.875rem;
  color: #6b7280;
  margin-bottom: 1.5rem;
}

.redirect-status {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.75rem;
}

.status-dots {
  display: flex;
  gap: 0.375rem;
}

