  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  background: #5B47FB;
  opacity: 0.3;
  animation: dotPulse 1.4s infinite;
}

.status-dot:nth-child(2) {
  animation-delay: 0.2s;
}

.status-dot:nth-child(3) {
  animation-delay: 0.4s;
}

.redirect-status p {
  font-size: 0.875rem;
  color: #6b7280;
}

/* Animations */
@keyframes pulse {
  0% {
    transform: translate(-50%, -50%) scale(0.95);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(1.2);
    opacity: 0;
  }
}

@keyframes dotPulse {
  0%, 100% {
    opacity: 0.3;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
}

