  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
  transform: translateX(100%);
  transition: all 0.4s ease;
  border-radius: 0.5rem;
  /* Ensure proper scaling */
  transform-origin: center;
  will-change: transform, opacity;
}

.screenshot.active {
  opacity: 1;
  transform: translateX(0) scale(1);
}

.screenshot.prev {
  transform: translateX(-100%) scale(0.8);
  opacity: 0;
}

/* Add smooth loading transition */
.screenshot.loading {
  opacity: 0;
  transform: scale(0.95);
}

/* Enhance screenshot presentation */
@media (min-width: 480px) {
  .screenshots-wrapper {
    max-width: 360px;
  }
}

/* Adjust for 4:3 aspect ratio when in modal mode */
.widget-container.aspect-ratio-4-3 .screenshots-wrapper {
  max-width: 280px;
}

.screenshot.active {
  opacity: 1;
  transform: translateX(0);
}

.screenshot.prev {
  transform: translateX(-100%);
}

.screenshot-controls {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem 0.5rem;
  align-items: center;
}

.screenshot-controls .btn-primary {
  min-width: 120px;
  height: 40px;
  font-weight: 500;
}

.screenshot-dots {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}

.screenshot-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #d1d5db;
  transition: all 0.3s ease;
  cursor: pointer;
}

.screenshot-dot.active {
  background: #5B47FB;
  transform: scale(1.2);
}

/* Additional improvements for 4:3 aspect ratio and smaller viewports */

/* -------------------------------------
   New keyframes and animation classes
   ------------------------------------- */

/* Fade + Slide In for step-panels */
@keyframes fadeSlideIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Fade Out + Slide Down */
@keyframes fadeSlideOut {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(20px);
  }
}

/* Scale for Checkmark */
@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Fade + Scale for logos */
@keyframes fadeScaleIn {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

/* Chevron wiggle animation */
@keyframes chevronMove {
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(5px);
  }
}

/* Slide-Fade In for newly added systems */
@keyframes slideFadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Scale In for alerts */
@keyframes scaleIn {
  0% {
    transform: scale(0.9);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Fade-in for institution name */
@keyframes fadeInText {
  0% {
    opacity: 0;
    transform: translateY(5px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Focus ring glow */
@keyframes focusGlow {
  0% {
    box-shadow: 0 0 0 0 rgba(91, 71, 251, 0.3);
  }
  100% {
    box-shadow: 0 0 0 6px rgba(91, 71, 251, 0.0);
  }
}

/* Smooth scroll for scroll-area */
.scroll-area {
  scroll-behavior: smooth;
}

/* .step-panel fade/slide transitions */
.step-panel {
  animation-duration: 0.4s;
  animation-fill-mode: both;
  transition: opacity 0.4s ease, transform 0.4s ease;
}

/* Show/hide states (handled in script) */
.step-panel.showing {
  animation-name: fadeSlideIn;
}
.step-panel.hiding {
  animation-name: fadeSlideOut;
}

/* Checkmark scale animation (renamed to scale-animation) */
.scale-animation {
  animation: scaleIn 0.2s ease-out forwards;
}

/* Fade & scale system logo on load */
.system-logo {
  opacity: 0;
  transform: scale(0.8);
  transition: opacity 0.3s, transform 0.3s;
}
.system-logo.loaded {
  opacity: 1;
  transform: scale(1);
}

/* Animate arrow wiggle in connect-new-btn */
.connect-new-btn .icon-arrow {
  animation: chevronMove 2s infinite;
}

/* Slide-Fade In for system card */
.slide-fade-in {
  animation: slideFadeIn 0.4s ease forwards;
}

/* Scale in for alerts */
.alert.scale-in {
  animation: scaleIn 0.3s ease forwards;
}

/* Institution name fade in */
.inst-name.fade-in {
  animation: fadeInText 0.3s forwards;
}

/* Card hover scale */
.system-card:hover {
  transform: scale(1.02);
}

/* Input focus ring animation (optional) */
.entry-input:focus {
  animation: focusGlow 0.3s forwards;
  border-color: #5B47FB;
}

/* Modify existing button hover and active states */
.btn:hover {
  filter: brightness(95%);
  transition: filter 0.2s;
}
.btn:active {
  transform: scale(0.95);
  transition: transform 0.05s;
}

/* Filter popover fade only */
.filters-popover {
  transform-origin: top center;
  transition: opacity 0.3s ease;
}
.filters-popover.show {
  display: block;
  opacity: 1;
}
.filters-popover.hide {
  display: none;
  opacity: 0;
}

/* Search highlight underline slide */
.highlighted {
  position: relative;
  color: #6C47FF;
  font-weight: 500;
}
.highlighted::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #6C47FF;
  transform: scaleX(0);
  transform-origin: left center;
  transition: transform 0.2s ease;
}
.highlighted:hover::after {
  transform: scaleX(1);
}

/* Ensure .icon-alert-circle bounces if needed */
.animate-bounce {
  animation: bounce 1s infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(-25%);
    animation-timing-function: cubic-bezier(0.8, 0, 1, 1);
  }
  50% {
    transform: translateY(0);
    animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
  }
}

/* Additional responsiveness: center layout for bigger screens */
@media (min-width: 768px) {
  .widget-container {
    margin: 2rem auto;
  }
}

/* Request Form Modal Styles */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.2);
  backdrop-filter: blur(4px);
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.modal-overlay.hidden {
  display: none;
}

.modal-overlay:not(.hidden) {
  opacity: 1;
}

.modal-content {
  background: #fff;
  border-radius: 1rem;
  position: relative;
  width: 90%;
  max-width: 480px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 25px -5px rgba(0,0,0,0.1), 0 10px 10px -5px rgba(0,0,0,0.04);
  transform: scale(0.95);
  opacity: 0;
  transition: all 0.3s ease;
}

.modal-overlay:not(.hidden) .modal-content {
  transform: scale(1);
  opacity: 1;
}

.request-form-modal {
  display: flex;
  flex-direction: column;
}

.modal-header {
  position: relative;
  padding: 1.5rem;
  border-bottom: 1px solid #e5e7eb;
}

.close-modal {
  position: absolute;
  top: 1rem;
  right: 1rem;
  opacity: 0.5;
  transition: opacity 0.2s ease;
}

.close-modal:hover {
  opacity: 1;
}

.modal-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #111827;
  margin-right: 2rem;
}

.modal-body {
  padding: 1.5rem;
}

.modal-description {
  color: #6b7280;
  margin-bottom: 1.5rem;
  line-height: 1.5;
}

.request-form {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-label {
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.required {
  color: #ef4444;
}

.form-input {
  border: 1px solid #d1d5db;
  padding: 0.625rem 0.75rem;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  transition: all 0.2s ease;
}

.form-input:hover {
  border-color: #9ca3af;
}

.form-input:focus {
  outline: none;
  border-color: #5B47FB;
  box-shadow: 0 0 0 3px rgba(91, 71, 251, 0.12);
}

.form-input.error {
  border-color: #ef4444;
}

.error-message {
  font-size: 0.75rem;
  color: #ef4444;
  min-height: 1rem;
}

.submit-request {
  margin-top: 0.5rem;
  height: 2.75rem;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-text {
  transition: opacity 0.2s ease;
}

.btn-loader {
  position: absolute;
  display: flex;
  align-items: center;
  gap: 0.25rem;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.btn-loader.visible {
  opacity: 1;
}

.loader-dot {
  width: 0.5rem;
  height: 0.5rem;
  background: currentColor;
  border-radius: 50%;
  animation: loaderDot 1.4s infinite ease-in-out;
}

.loader-dot:nth-child(2) {
  animation-delay: 0.2s;
}

.loader-dot:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes loaderDot {
  0%, 100% {
    opacity: 0.3;
    transform: scale(0.8);
  }
  50% {
    opacity: 1;
    transform: scale(1);
  }
}

/* Mobile Optimizations */
@media (max-width: 480px) {
  .modal-content {
    width: 95%;
    max-width: 440px;
    max-height: 90vh;
    border-radius: 0.5rem;
  }

  .modal-header {
    padding: 1.25rem;
  }

  .modal-body {
    padding: 1.25rem;
  }

  .modal-title {
    font-size: 1.25rem;
  }
}

@media (max-width: 768px) {
